$loading-width: 180px;

.loading {
  display: block;
  position: fixed;
  z-index: 98;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000009f7;
}

.loading > div {
  display: block;
  position: absolute;
  top: 0;
  margin-top: calc(50vh - (#{$loading-width} / 2));
  margin-left: calc(50vw - (#{$loading-width} / 2));
  width: #{$loading-width};
  height: #{$loading-width};
  z-index: 99;
  border-radius: 20px;

  .loading-spinner {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 4px solid #ffffff;
    border-top-color: #797777;
    animation: spin 1s infinite linear;
    margin: auto;
    margin-top: 30px;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  p {
    color: #eff0f2;
    font-family: Audiowide;
    text-align: center;
    margin-top: 30px;
    font-size: 25px;
  }
}
